exports.push([module.id, "\n.slider-btn[data-v-6d4bc492] {\r\n  position: absolute;\r\n  width: 40px;\r\n  height: 40px;\r\n  left: 0;\r\n  top: 0;\r\n  z-index: 12;\r\n  cursor: pointer;\r\n  /* background-image: url(../assets/sprite.3.2.0.png); */\r\n  background-position: 0 -84px;\r\n  transition: inherit;\r\n  border: 1px solid #0055f3;\r\n  border-radius: 4px;\r\n  background: #c8d7f3;\r\n  box-sizing: border-box;\n}\n.slider-btn[data-v-6d4bc492]:hover {\r\n  background: #0055f3;\n}\n.slider-btn:hover .right-arrow1[data-v-6d4bc492] {\r\n  border-left: 8px #fff solid;\n}\n.slider-btn:hover .right-arrow2[data-v-6d4bc492] {\r\n  border-left: 8px #0055f3 solid;\n}\n.slider-ok .right-arrow1[data-v-6d4bc492] {\r\n  border-left: 8px #fff solid;\n}\n.slider-ok .right-arrow2[data-v-6d4bc492] {\r\n  border-left: 8px #0055f3 solid;\n}\n.slider-ok[data-v-6d4bc492]{\r\n\tbackground: #0055f3;\n}\n.slider-btn b[data-v-6d4bc492] {\r\n  position: absolute;\r\n  top: 50%;\r\n  left: 50%;\r\n  transform: translate(-50%, -50%);\r\n  width: 8px;\r\n  height: 16px;\n}\n.right-ok[data-v-6d4bc492]{\r\n\tposition: relative;\n}\n.right-ok[data-v-6d4bc492]:after{\r\n\tcontent:\"\";\r\n\tposition: absolute;\r\n\ttop:5px;\r\n\tleft:50%;\r\n\ttransform: translate(-50%,-50%) rotate(45deg);\r\n\theight:16px;\r\n\twidth:8px;\r\n\tborder:1px solid #fff;\r\n\tborder-width:0 2px 3px 0;\n}\n.right-arrow1[data-v-6d4bc492],\r\n.right-arrow2[data-v-6d4bc492] {\r\n  width: 0;\r\n  height: 0;\r\n  display: block;\r\n  position: absolute;\r\n  left: 0;\r\n  top: 0;\r\n  border-top: 8px transparent dashed;\r\n  border-right: 8px transparent dashed;\r\n  border-bottom: 8px transparent dashed;\r\n  border-left: 8px white solid;\r\n  overflow: hidden;\n}\n.right-arrow1[data-v-6d4bc492] {\r\n  left: 1px;\r\n  /*重要*/\r\n  border-left: 8px #0055f3 solid;\n}\n.right-arrow2[data-v-6d4bc492] {\r\n  border-left: 8px #c8d7f3 solid;\n}\n.ver-tips[data-v-6d4bc492] {\r\n  position: absolute;\r\n  left: 0;\r\n  bottom: -22px;\r\n  background: rgba(255, 255, 255, 0.9);\r\n  height: 22px;\r\n  line-height: 22px;\r\n  font-size: 12px;\r\n  width: 100%;\r\n  margin: 0;\r\n  text-align: left;\r\n  padding: 0 8px;\r\n  transition: all 0.4s;\n}\n.slider-tips[data-v-6d4bc492] {\r\n  bottom: 0;\n}\n.ver-tips i[data-v-6d4bc492] {\r\n  display: inline-block;\r\n  width: 22px;\r\n  height: 22px;\r\n  vertical-align: top;\r\n  /* background-image: url(../assets/sprite.3.2.0.png); */\r\n  background-position: -4px -1229px;\n}\n.ver-tips span[data-v-6d4bc492] {\r\n  display: inline-block;\r\n  vertical-align: top;\r\n  line-height: 22px;\r\n  color: #455;\n}\n.active-tips[data-v-6d4bc492] {\r\n  display: block;\n}\n.hidden[data-v-6d4bc492] {\r\n  display: none;\n}\n.puzzle-container[data-v-6d4bc492] {\r\n  position: relative;\r\n  display: inline-block;\r\n  /* padding: 15px 15px 28px; */\r\n  /* border: 1px solid #ddd; */\r\n  background: #ffffff;\r\n  border-radius: 16px;\n}\n.puzzle-header[data-v-6d4bc492] {\r\n  display: flex;\r\n  justify-content: space-between;\r\n  margin: 5px 0;\n}\n.change[data-v-6d4bc492] {\r\n  position: absolute;\r\n  bottom: 40px;\r\n  left: 0;\n}\n.puzzle-header-left[data-v-6d4bc492] {\r\n  color: #333;\n}\n.re-btn[data-v-6d4bc492],\r\n.close-btn[data-v-6d4bc492] {\r\n  font-size: 16px;\r\n  cursor: pointer;\r\n  color: #666;\n}\n.c-s[data-v-6d4bc492] {\r\n  position: absolute;\r\n  top: 10px;\r\n  right: 10px;\r\n  z-index: 100;\n}\n.re-btn[data-v-6d4bc492]:hover {\r\n  color: #67c23a;\n}\n.close-btn[data-v-6d4bc492]:hover {\r\n  color: #f56c6c;\n}\n.close-btn[data-v-6d4bc492] {\r\n  margin-left: 5px;\n}\n.slider-container[data-v-6d4bc492] {\r\n  position: relative;\r\n  margin: 0 auto 0;\r\n  min-height: 15px;\r\n  height: 40px;\n}\n.old_s[data-v-6d4bc492] {\r\n  position: absolute;\r\n  height: 40px;\r\n  width: 0;\r\n  background: #c8d7f3;\n}\n.slider-bar[data-v-6d4bc492] {\r\n  height: 40px;\r\n  /* border: 1px solid #c3c3c3; */\r\n  border-radius: 2px;\r\n  background: #e4e9f3;\r\n  /* box-shadow: 0 1px 1px rgba(12, 10, 10, 0.2) inset; */\r\n  position: absolute;\r\n  width: 100%;\r\n  top: 0;\r\n  text-align: center;\r\n  line-height: 40px;\r\n  color: #8192a8;\r\n  font-size: 14px;\n}\n#puzzle-box[data-v-6d4bc492] {\r\n  position: absolute;\r\n  left: 0;\r\n  top: 0;\r\n  z-index: 22;\n}\n#puzzle-shadow[data-v-6d4bc492] {\r\n  position: absolute;\r\n  left: 0;\r\n  top: 0;\r\n  z-index: 22;\n}\n#puzzle-lost[data-v-6d4bc492] {\r\n  position: absolute;\r\n  left: 0;\r\n  top: 0;\r\n  z-index: 33;\n}\n.puzzle-lost-box[data-v-6d4bc492] {\r\n  position: absolute;\r\n  width: 260px;\r\n  height: 116px;\r\n  left: 0;\r\n  top: 0;\r\n  z-index: 111;\n}\n@font-face {\r\n\tfont-family: \"iconfont\";\r\n\tsrc: url(" + escape(require("./icon-font/iconfont.eot?t=1565160368550")) + "); /* IE9 */\r\n\tsrc: url(" + escape(require("./icon-font/iconfont.eot?t=1565160368550")) + "#iefix)\r\n\t\t\tformat(\"embedded-opentype\"),\r\n\t\t\r\n\t\t\turl(\"data:application/x-font-woff2;charset=utf-8;base64,\")\r\n\t\t\tformat(\"woff2\"),\r\n\t\turl(" + escape(require("./icon-font/iconfont.woff?t=1565160368550")) + ") format(\"woff\"),\r\n\t\turl(" + escape(require("./icon-font/iconfont.ttf?t=1565160368550")) + ") format(\"truetype\"),\r\n\t\t\r\n\t\t\turl(" + escape(require("./icon-font/iconfont.svg?t=1565160368550")) + "#iconfont)\r\n\t\t\tformat(\"svg\"); /* iOS 4.1- */\n}\n.iconfont[data-v-6d4bc492] {\r\n  font-family: \"iconfont\" !important;\r\n  font-size: 16px;\r\n  font-style: normal;\r\n  -webkit-font-smoothing: antialiased;\r\n  -moz-osx-font-smoothing: grayscale;\n}\n.icon-guanbi[data-v-6d4bc492]:before {\r\n  content: \"\\F01F1\";\n}\n.icon-shuaxin[data-v-6d4bc492]:before {\r\n  content: \"\\E609\";\n}\nimg[data-v-6d4bc492] {\r\n  vertical-align: top;\n}\r\n", "", {"version":3,"sources":["F:/learn/node_test/mr-vue-puzzle/src/src/puzzleVerification.vue"],"names":[],"mappings":";AAsgBA;EACA,mBAAA;EACA,YAAA;EACA,aAAA;EACA,QAAA;EACA,O