te>\r\n  <div @mouseleave=\"bLeave\" class=\"puzzle-container\" v-show=\"isVerificationShow\">\r\n    <div class=\"change\">\r\n      <div v-show=\"isBoxShow\" class=\"puzzle-header\">\r\n        <!-- <span class=\"puzzle-header-left\">拖动下方滑块完成拼图</span> -->\r\n        <div>\r\n          <!-- <span class=\"re-btn iconfont icon-shuaxin\" @click=\"refreshImg\"></span> -->\r\n          <!-- <span class=\"close-btn iconfont icon-guanbi\" @click=\"closeVerificationBox\"></span> -->\r\n        </div>\r\n      </div>\r\n      <div v-show=\"isBoxShow\" :style=\"'position:relative;overflow:hidden;width:'+ dataWidth +'px;'\">\r\n        <span class=\"re-btn iconfont icon-shuaxin c-s\" @click=\"refreshImg\"></span>\r\n        <div :style=\"'position:relative;width:' + dataWidth + 'px;height:' + dataHeight + 'px;'\">\r\n          <img\r\n            id=\"scream\"\r\n            ref=\"scream\"\r\n            :src=\"imgRandom\"\r\n            :style=\"'width:' + dataWidth + 'px;height:' + dataHeight + 'px;'\"\r\n          />\r\n          <canvas id=\"puzzle-box\" ref=\"puzzleBox\" :width=\"dataWidth\" :height=\"dataHeight\"></canvas>\r\n        </div>\r\n        <div\r\n          class=\"puzzle-lost-box\"\r\n          :style=\"'left:' + left_Num + 'px;width:' + dataWidth + 'px;height:' + dataHeight + 'px;'\"\r\n        >\r\n          <canvas id=\"puzzle-shadow\" ref=\"puzzleShadow\" :width=\"dataWidth\" :height=\"dataHeight\"></canvas>\r\n          <canvas id=\"puzzle-lost\" ref=\"puzzleLost\" :width=\"dataWidth\" :height=\"dataHeight\"></canvas>\r\n        </div>\r\n        <p :class=\"'ver-tips'+ (displayTips ? ' slider-tips' : '')\" ref=\"verTips\">\r\n          <template v-if=\"verification\">\r\n            <i style=\"background-position:-4px -1207px;\"></i>\r\n            <span style=\"color:#42ca6b;\">验证通过</span>\r\n            <span></span>\r\n          </template>\r\n          <template v-if=\"!verification\">\r\n            <i style=\"background-position:-4px -1229px;\"></i>\r\n            <span style=\"color:red;\">验证失败:</span>\r\n            <span style=\"margin-left:4px;\">拖动滑块将悬浮图像正确拼合</span>\r\n          </template>\r\n        </p>\r\n      </div>\r\n    </div>\r\n\r\n    <div class=\"slider-container\" :style=\"'width:' + dataWidth + 'px;'\">\r\n      <div class=\"slider-bar\">{{cTitle}}</div>\r\n      <div\r\n        class=\"slider-btn arraw\"\r\n\t\t\t\t:class=\"{'slider-ok':forbid}\"\r\n        ref=\"sliderBtn\"\r\n        @mouseenter=\"bEnter\"\r\n        @mousedown=\"startMove\"\r\n        @touchstart=\"startMove\"\r\n      >\r\n        <b v-show=\"!forbid\" class=\"right\">\r\n          <i class=\"right-arrow1\"></i>\r\n          <i class=\"right-arrow2\"></i>\r\n        </b>\r\n        <b v-show=\"forbid\" class=\"right-ok\">\r\n          \r\n        </b>\r\n      </div>\r\n      <div class=\"old_s\" ref=\"oldWidth\" :style=\"'width:' + dataOldWidth + 'px;'\"></div>\r\n    </div>\r\n  </div>\r\n</template>\r\n\r\n<script>\r\nexport default {\r\n  name: \"puzzleVerification\",\r\n  data() {\r\n    return {\r\n      isVerificationShow: false,\r\n      isBoxShow: false,\r\n      moveStart: \"\",\r\n      displayTips: false,\r\n      verification: false,\r\n      dataOldWidth: 0,\r\n      randomX: null,\r\n      randomY: null,\r\n      imgRandom: \"\",\r\n      left_Num: 0,\r\n      dataWidth: null,\r\n      dataHeight: null,\r\n      puzzleSize: null, // 滑块的大小\r\n      deviationValue: null,\r\n      radius: null,\r\n      padding: null\r\n    };\r\n  },\r\n  model: {\r\n    prop: \"verificationShow\",\r\n    event: \"setVisible\"\r\n  },\r\n  watch: {\r\n    isVerificationShow(val) {\r\n      this.$emit(\"setVisible\", val);\r\n    },\r\n    verificationShow(val) {\r\n      this.isVerificationShow = val;\r\n    }\r\n  },\r\n  computed: {\r\n    cTitle() {\r\n      return this.title;\r\n    }\r\n  },\r\n  props: {\r\n    // 画布图片的尺寸\r\n    width: {\r\n      type: [String, Number],\r\n      default: 380\r\n    },\r\n    forbid: {\r\n      type: Boolean,\r\n      default: false\r\n    },\r\n    title: {\r\n      type: String,\r\n      default: \"向右滑动滑块填充拼图\"\r\n    },\r\n    height: {\r\n      type: [String, Number],\r\n      default: 120\r\n    },\r\n    // 图集\r\n    puzzleImgList: {\r\n      type: Array,\r\n      default: () => [\r\n        \"https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1551244602306&di=5b40d29f1de52815d2643ce3eb3f6d3b&imgtype=0&src=http%3A%2F%2Fimg1.3lian.com%2F2015%2Fa1%2F38%2Fd%2F168.jpg\",\r\n        \"https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1551244644208&di=3f09dbe3476994f15ed207e4d0c008ef&imgtype=0&src=http%3A%2F%2Fpic3.16pic.com%2F00%2F47%2F90%2F16pic_4790939_b.jpg\",\r\n        \"https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1551244821054&di=bf03257cfaf9d0a0f020e7f1363cf5f8&imgtype=0&src=http%3A%2F%2Fpic.58pic.com%2F58pic%2F15%2F11%2F96%2F88W58PIC7Y2_1024.jpg\"\r\n      ]\r\n    },\r\n    // 滑块的大小\r\n    blockSize: {\r\n      type: [String, Number],\r\n      default: 40\r\n    },\r\n    // 误差\r\n    deviation: {\r\n      type: [String, Number],\r\n      default: 4\r\n    },\r\n    // 滑块的圆角大小\r\n    blockRadius: {\r\n      type: [String, Number],\r\n      default: 4\r\n    },\r\n    // 滑块随机出现的范围\r\n    wraperPadding: {\r\n      type: [String, Number],\r\n      default: 20\r\n    },\r\n    // 滑块形状 square  puzzle\r\n    blockType: {\r\n      type: String,\r\n      default: \"square\"\r\n    },\r\n    // 成功的回调\r\n    onSuccess: {\r\n      type: Function,\r\n      default: () => {\r\n        // console.log(\"成功\");\r\n      }\r\n    },\r\n    // 失败的回调\r\n    onError: {\r\n      type: Function,\r\n      default: () => {\r\n        // console.log(\"失败\");\r\n      }\r\n    },\r\n    verificationShow: {\r\n      type: Boolean,\r\n      default: false\r\n    }\r\n  },\r\n  mounted() {\r\n    this.$nextTick(() => {\r\n      this.initCanvas();\r\n    });\r\n  },\r\n  created() {\r\n    // 随机显示一张图片\r\n    let imgRandomIndex = Math.round(\r\n      Math.random() * (this.puzzleImgList.length - 1)\r\n    );\r\n    this.imgRandom = this.puzzleImgList[imgRandomIndex];\r\n\r\n    this.puzzleSize = Number(this.blockSize);\r\n    this.deviationValue = Number(this.deviation);\r\n    this.radius = Number(this.blockRadius);\r\n    this.dataWidth = Number(this.width);\r\n    this.dataHeight = Number(this.height);\r\n    this.padding = Number(this.wraperPadding);\r\n  },\r\n  methods: {\r\n    bEnter() {\r\n\t\t\tif(this.forbid){\r\n\t\t\t\treturn\r\n\t\t\t}\r\n      this.isBoxShow = true;\r\n    },\r\n    bLeave() {\r\n      this.isBoxShow = false;\r\n    },\r\n    /* 关闭验证 */\r\n    closeVerificationBox() {\r\n      this.isVerificationShow = false;\r\n    },\r\n    /* 刷新 */\r\n    refreshImg() {\r\n      let imgRandomIndex = Math.round(\r\n        Math.random() * (this.puzzleImgList.length - 1)\r\n      );\r\n      this.imgRandom = this.puzzleImgList[imgRandomIndex];\r\n      this.initCanvas();\r\n    },\r\n    /* 画布初始化 */\r\n    initCanvas() {\r\n      this.clearCanvas();\r\n      let w = this.dataWidth;\r\n      let h = this.dataHeight;\r\n      let PL_Size = this.puzzleSize;\r\n      let padding = this.padding;\r\n      let MinN_X = padding + PL_Size;\r\n      let MaxN_X = w - padding - PL_Size - PL_Size / 6;\r\n      let MaxN_Y = padding;\r\n      let MinN_Y = h - padding - PL_Size - PL_Size / 6;\r\n      this.randomX = Math.round(Math.random() * (MaxN_X - PL_Size) + MinN_X);\r\n      this.randomY = Math.round(Math.random() * MaxN_Y + MinN_Y);\r\n      let X = this.randomX;\r\n      let Y = this.randomY;\r\n      this.left_Num = -X + 10;\r\n      let d = PL_Size / 3;\r\n      let radius = Number(this.radius);\r\n\r\n      let c = this.$refs.puzzleBox;\r\n      let c_l = this.$refs.puzzleLost;\r\n      let c_s = this.$refs.puzzleShadow;\r\n      let ctx = c.getContext(\"2d\");\r\n      let ctx_l = c_l.getContext(\"2d\");\r\n      let ctx_s = c_s.getContext(\"2d\");\r\n      ctx.globalCompositeOperation = \"xor\";\r\n      ctx.shadowBlur = 10;\r\n      ctx.shadowColor = \"#fff\";\r\n      ctx.shadowOffsetX = 3;\r\n      ctx.shadowOffsetY = 3;\r\n      ctx.fillStyle = \"rgba(0,0,0,0.7)\";\r\n      ctx.beginPath();\r\n      ctx.lineWidth = \"1\";\r\n      ctx.strokeStyle = \"rgba(0,0,0,0)\";\r\n      if (this.blockType === \"square\") {\r\n        ctx.arc(X + radius, Y + radius, radius, Math.PI, (Math.PI * 3) / 2);\r\n        ctx.lineTo(PL_Size - radius + X, Y);\r\n        ctx.arc(\r\n          PL_Size - radius + X,\r\n          radius + Y,\r\n          radius,\r\n          (Math.PI * 3) / 2,\r\n          Math.PI * 2\r\n        );\r\n        ctx.lineTo(PL_Size + X, PL_Size + Y - radius);\r\n        ctx.arc(\r\n          PL_Size - radius + X,\r\n          PL_Size - radius + Y,\r\n          radius,\r\n          0,\r\n          (Math.PI * 1) / 2\r\n        );\r\n        ctx.lineTo(radius + X, PL_Size + Y);\r\n        ctx.arc(\r\n          radius + X,\r\n          PL_Size - radius + Y,\r\n          radius,\r\n          (Math.PI * 1) / 2,\r\n          Math.PI\r\n        );\r\n      } else {\r\n        ctx.moveTo(X, Y);\r\n        ctx.lineTo(X + d, Y);\r\n        ctx.bezierCurveTo(X + d, Y - d, X + 2 * d, Y - d, X + 2 * d, Y);\r\n        ctx.lineTo(X + 3 * d, Y);\r\n        ctx.lineTo(X + 3 * d, Y + d);\r\n        ctx.bezierCurveTo(\r\n          X + 2 * d,\r\n          Y + d,\r\n          X + 2 * d,\r\n          Y + 2 * d,\r\n          X + 3 * d,\r\n          Y + 2 * d\r\n        );\r\n        ctx.lineTo(X + 3 * d, Y + 3 * d);\r\n        ctx.lineTo(X, Y + 3 * d);\r\n      }\r\n      ctx.closePath();\r\n      ctx.stroke();\r\n      ctx.fill();\r\n\r\n      let img = new Image();\r\n      img.src = this.imgRandom;\r\n\r\n      img.onload = function() {\r\n        ctx_l.drawImage(img, 0, 0, w, h);\r\n      };\r\n      ctx_l.beginPath();\r\n      ctx_l.strokeStyle = \"rgba(0,0,0,0)\";\r\n      if (this.blockType === \"square\") {\r\n        ctx_l.arc(X + radius, Y + radius, radius, Math.PI, (Math.PI * 3) / 2);\r\n        ctx_l.lineTo(PL_Size - radius + X, Y);\r\n        ctx_l.arc(\r\n          PL_Size - radius + X,\r\n          radius + Y,\r\n          radius,\r\n          (Math.PI * 3) / 2,\r\n          Math.PI * 2\r\n        );\r\n        ctx_l.lineTo(PL_Size + X, PL_Size + Y - radius);\r\n        ctx_l.arc(\r\n          PL_Size - radius + X,\r\n          PL_Size - radius + Y,\r\n          radius,\r\n          0,\r\n          (Math.PI * 1) / 2\r\n        );\r\n        ctx_l.lineTo(radius + X, PL_Size + Y);\r\n        ctx_l.arc(\r\n          radius + X,\r\n          PL_Size - radius + Y,\r\n          radius,\r\n          (Math.PI * 1) / 2,\r\n          Math.PI\r\n        );\r\n      } else {\r\n        ctx_l.moveTo(X, Y);\r\n        ctx_l.lineTo(X + d, Y);\r\n        ctx_l.bezierCurveTo(X + d, Y - d, X + 2 * d, Y - d, X + 2 * d, Y);\r\n        ctx_l.lineTo(X + 3 * d, Y);\r\n        ctx_l.lineTo(X + 3 * d, Y + d);\r\n        ctx_l.bezierCurveTo(\r\n          X + 2 * d,\r\n          Y + d,\r\n          X + 2 * d,\r\n          Y + 2 * d,\r\n          X + 3 * d,\r\n          Y + 2 * d\r\n        );\r\n        ctx_l.lineTo(X + 3 * d, Y + 3 * d);\r\n        ctx_l.lineTo(X, Y + 3 * d);\r\n      }\r\n      ctx_l.closePath();\r\n      ctx_l.stroke();\r\n      ctx_l.shadowBlur = 10;\r\n      ctx_l.shadowColor = \"black\";\r\n      ctx_l.clip();\r\n      ctx_s.beginPath();\r\n      ctx_s.lineWidth = \"1\";\r\n      ctx_s.strokeStyle = \"rgba(0,0,0,0)\";\r\n      if (this.blockType === \"square\") {\r\n        ctx_s.arc(X + radius, Y + radius, radius, Math.PI, (Math.PI * 3) / 2);\r\n        ctx_s.lineTo(PL_Size - radius + X, Y);\r\n        ctx_s.arc(\r\n          PL_Size - radius + X,\r\n          radius + Y,\r\n          radius,\r\n          (Math.PI * 3) / 2,\r\n          Math.PI * 2\r\n        );\r\n        ctx_s.lineTo(PL_Size + X, PL_Size + Y - radius);\r\n        ctx_s.arc(\r\n          PL_Size - radius + X,\r\n          PL_Size - radius + Y,\r\n          radius,\r\n          0,\r\n          (Math.PI * 1) / 2\r\n        );\r\n        ctx_s.lineTo(radius + X, PL_Size + Y);\r\n        ctx_s.arc(\r\n          radius + X,\r\n          PL_Size - radius + Y,\r\n          radius,\r\n          (Math.PI * 1) / 2,\r\n          Math.PI\r\n        );\r\n      } else {\r\n        ctx_s.moveTo(X, Y);\r\n        ctx_s.lineTo(X + d, Y);\r\n        ctx_s.bezierCurveTo(X + d, Y - d, X + 2 * d, Y - d, X + 2 * d, Y);\r\n        ctx_s.lineTo(X + 3 * d, Y);\r\n        ctx_s.lineTo(X + 3 * d, Y + d);\r\n        ctx_s.bezierCurveTo(\r\n          X + 2 * d,\r\n          Y + d,\r\n          X + 2 * d,\r\n          Y + 2 * d,\r\n          X + 3 * d,\r\n          Y + 2 * d\r\n        );\r\n        ctx_s.lineTo(X + 3 * d, Y + 3 * d);\r\n        ctx_s.lineTo(X, Y + 3 * d);\r\n      }\r\n      ctx_s.closePath();\r\n      ctx_s.stroke();\r\n      ctx_s.shadowBlur = 20;\r\n      ctx_s.shadowColor = \"black\";\r\n      ctx_s.fill();\r\n    },\r\n    /* 通过重置画布尺寸清空画布，这种方式更彻底 */\r\n    clearCanvas() {\r\n      let c = this.$refs.puzzleBox;\r\n      let c_l = this.$refs.puzzleLost;\r\n      let c_s = this.$refs.puzzleShadow;\r\n      c.setAttribute(\"height\", c.getAttribute(\"height\"));\r\n      c_l.setAttribute(\"height\", c.getAttribute(\"height\"));\r\n      c_s.setAttribute(\"height\", c.getAttribute(\"height\"));\r\n    },\r\n    /* 按住滑块后初始化移动监听，记录初始位置 */\r\n    startMove(e) {\r\n      // console.log(e);\r\n      if (this.forbid) {\r\n        return;\r\n      }\r\n      e = e || window.event;\r\n      this.$refs.sliderBtn.style.backgroundPosition = \"0 -216px\";\r\n      this.moveStart = e.pageX || e.targetTouches[0].pageX;\r\n      // this.isBoxShow = true;\r\n      this.addMouseMoveListener();\r\n    },\r\n    /* 滑块移动 */\r\n    moving(e) {\r\n      let self = this;\r\n      e = e || window.event;\r\n      let moveX = e.pageX || e.targetTouches[0].pageX;\r\n      let d = moveX - self.moveStart;\r\n      let w = self.dataWidth;\r\n      let PL_Size = this.puzzleSize;\r\n      let padding = this.padding;\r\n      if (self.moveStart === \"\") {\r\n        return \"\";\r\n      }\r\n      if (d < 0 || d > w - padding - PL_Size) {\r\n        return \"\";\r\n      }\r\n      self.$refs.sliderBtn.style.left = d + \"px\";\r\n      self.$refs.oldWidth.style.transition = \"inherit\";\r\n      self.$refs.sliderBtn.style.transition = \"inherit\";\r\n      self.$refs.puzzleLost.style.left = d + \"px\";\r\n      self.$refs.puzzleLost.style.transition = \"inherit\";\r\n      self.$refs.puzzleShadow.style.left = d + \"px\";\r\n      self.$refs.puzzleShadow.style.transition = \"inherit\";\r\n      self.dataOldWidth = d;\r\n      // console.log(d);\r\n      // console.log(self.dataOldWidth);\r\n    },\r\n    /* 移动结束，验证并回调 */\r\n    moveEnd(e) {\r\n      let self = this;\r\n      e = e || window.event;\r\n      let moveEnd_X = (e.pageX || e.changedTouches[0].pageX) - self.moveStart;\r\n      let ver_Num = self.randomX - 10;\r\n      let deviationValue = this.deviationValue;\r\n      let Min_left = ver_Num - deviationValue;\r\n      let Max_left = ver_Num + deviationValue;\r\n      if (self.moveStart !== \"\") {\r\n        // self.isBoxShow = false;\r\n        if (Max_left > moveEnd_X && moveEnd_X > Min_left) {\r\n          self.displayTips = true;\r\n          self.verification = true;\r\n          setTimeout(function() {\r\n            self.displayTips = false;\r\n            // self.initCanvas();\r\n            /* 成功的回调函数 */\r\n            self.onSuccess();\r\n          }, 500);\r\n        } else {\r\n          self.displayTips = true;\r\n\t\t\t\t\tself.verification = false;\r\n\t\t\t\t\tsetTimeout(function() {\r\n          self.$refs.sliderBtn.style.left = 0;\r\n          self.dataOldWidth = 0;\r\n          self.$refs.oldWidth.style.transition = \"width 0.5s\";\r\n          self.$refs.sliderBtn.style.transition = \"left 0.5s\";\r\n          self.$refs.puzzleLost.style.left = 0;\r\n          self.$refs.puzzleLost.style.transition = \"left 0.5s\";\r\n          self.$refs.puzzleShadow.style.left = 0;\r\n          self.$refs.puzzleShadow.style.transition = \"left 0.5s\";\r\n        }, 400);\r\n          setTimeout(function() {\r\n            self.displayTips = false;\r\n            self.initCanvas();\r\n            /* 失败的回调函数 */\r\n            self.onError();\r\n          }, 800);\r\n        }\r\n      }\r\n      if (\r\n        typeof self.$refs.sliderBtn !== \"undefined\" &&\r\n        typeof self.$refs.puzzleLost !== \"undefined\" &&\r\n        typeof self.$refs.puzzleShadow !== \"undefined\"\r\n      ) {\r\n        \r\n        self.$refs.sliderBtn.style.backgroundPosition = \"0 -84px\";\r\n      }\r\n      self.moveStart = \"\";\r\n    },\r\n    /* 全局绑定滑块移动与滑动结束，移动过程中鼠标可在页面任何位置 */\r\n    addMouseMoveListener() {\r\n      let self = this;\r\n      document.addEventListener(\"mousemove\", self.moving);\r\n      document.addEventListener(\"touchmove\", self.moving);\r\n      document.addEventListener(\"mouseup\", self.moveEnd);\r\n      document.addEventListener(\"touchend\", self.moveEnd);\r\n    }\r\n  }\r\n};\r\n</script>\r\n\r\n<style scoped>\r\n.slider-btn {\r\n  position: absolute;\r\n  width: 40px;\r\n  height: 40px;\r\n  left: 0;\r\n  top: 0;\r\n  z-index: 12;\r\n  cursor: pointer;\r\n  /* background-image: url(../assets/sprite.3.2.0.png); */\r\n  background-position: 0 -84px;\r\n  transition: inherit;\r\n  border: 1px solid #0055f3;\r\n  border-radius: 4px;\r\n  background: #c8d7f3;\r\n  box-sizing: border-box;\r\n}\r\n.slider-btn:hover {\r\n  background: #0055f3;\r\n}\r\n.slider-btn:hover .right-arrow1 {\r\n  border-left: 8px #fff solid;\r\n}\r\n.slider-btn:hover .right-arrow2 {\r\n  border-left: 8px #0055f3 solid;\r\n}\r\n.slider-ok .right-arrow1 {\r\n  border-left: 8px #fff solid;\r\n}\r\n.slider-ok .right-arrow2 {\r\n  border-left: 8px #0055f3 solid;\r\n}\r\n.slider-ok{\r\n\tbackground: #0055f3;\r\n}\r\n.slider-btn b {\r\n  position: absolute;\r\n  top: 50%;\r\n  left: 50%;\r\n  transform: translate(-50%, -50%);\r\n  width: 8px;\r\n  height: 16px;\r\n}\r\n\r\n.right-ok{\r\n\tposition: relative;\r\n}\r\n.right-ok:after{\r\n\tcontent:\"\";\r\n\tposition: absolute;\r\n\ttop:5px;\r\n\tleft:50%;\r\n\ttransform: translate(-50%,-50%) rotate(45deg);\r\n\theight:16px;\r\n\twidth:8px;\r\n\tborder:1px solid #fff;\r\n\tborder-width:0 2px 3px 0;\r\n}\r\n.right-arrow1,\r\n.right-arrow2 {\r\n  width: 0;\r\n  height: 0;\r\n  display: block;\r\n  position: absolute;\r\n  left: 0;\r\n  top: 0;\r\n  border-top: 8px transparent dashed;\r\n  border-right: 8px transparent dashed;\r\n  border-bottom: 8px transparent dashed;\r\n  border-left: 8px white solid;\r\n  overflow: hidden;\r\n}\r\n\r\n.right-arrow1 {\r\n  left: 1px;\r\n  /*重要*/\r\n  border-left: 8px #0055f3 solid;\r\n}\r\n\r\n.right-arrow2 {\r\n  border-left: 8px #c8d7f3 solid;\r\n}\r\n\r\n.ver-tips {\r\n  position: absolute;\r\n  left: 0;\r\n  bottom: -22px;\r\n  background: rgba(255, 255, 255, 0.9);\r\n  height: 22px;\r\n  line-height: 22px;\r\n  font-size: 12px;\r\n  width: 100%;\r\n  margin: 0;\r\n  text-align: left;\r\n  padding: 0 8px;\r\n  transition: all 0.4s;\r\n}\r\n\r\n.slider-tips {\r\n  bottom: 0;\r\n}\r\n\r\n.ver-tips i {\r\n  display: inline-block;\r\n  width: 22px;\r\n  height: 22px;\r\n  vertical-align: top;\r\n  /* background-image: url(../assets/sprite.3.2.0.png); */\r\n  background-position: -4px -1229px;\r\n}\r\n\r\n.ver-tips span {\r\n  display: inline-block;\r\n  vertical-align: top;\r\n  line-height: 22px;\r\n  color: #455;\r\n}\r\n\r\n.active-tips {\r\n  display: block;\r\n}\r\n\r\n.hidden {\r\n  display: none;\r\n}\r\n\r\n.puzzle-container {\r\n  position: relative;\r\n  display: inline-block;\r\n  /* padding: 15px 15px 28px; */\r\n  /* border: 1px solid #ddd; */\r\n  background: #ffffff;\r\n  border-radius: 16px;\r\n}\r\n\r\n.puzzle-header {\r\n  display: flex;\r\n  justify-content: space-between;\r\n  margin: 5px 0;\r\n}\r\n.